.card {
  border-radius: 10px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.8);
  padding: 5px 10px;
  margin: 0 20px 20px 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
  color: #434A54;
  cursor: pointer;
  -webkit-transition: .2s;
  transition: .2s;
}

.card:hover {
  background: #37BD9C;
  color: white;
}

.card svg {
  margin-right: auto;
  font-size: 40px;
}

.card h3 {
  margin-right: auto;
  font-size: 1.6em;
}
